import React from "react";

import './ExtendedMenu.scss';

export default class ExtendedMenu extends React.Component {
    render() {
        const menuItems = this.props.menuItems;

        const menuItemsJSX = menuItems.map((menuItem, index) => {
            return (
                <li key={index} className="extended-menu__item"><a href="/">{menuItem}</a></li>
            )
        });
        return (
            <nav className="extended-menu">
                <ul className="extended-menu__list">
                    {menuItemsJSX}
                </ul>
            </nav>
        )
    }
}