import React from "react";
import Logo from "../../../../Shared/Logo/Logo";

import './Footer.scss';
import Menu from "../Menu/Menu";
import Contacts from "../Contacts/Contacts";
import ExtendedMenu from "../ExtendedMenu/ExtendedMenu";
import Facebook from "../../../../Shared/icons/Facebook/Facebook";
import Linkedin from "../../../../Shared/icons/Linkedin/Linkedin";
import Instagram from "../../../../Shared/icons/Instagram/Instagram";
import Twitter from "../../../../Shared/icons/Twitter/Twitter";
import Pinterest from "../../../../Shared/icons/Pinterest/Pinterest";

export default class Footer extends React.Component {
    state = {
      currentLanguage: 'en',
    };

    languageChanger = (language) => {
        if (this.state.currentLanguage === language) return;
        //console.log(language);
        this.setState({ currentLanguage: language });
        this.props.languageChanger(language);
    };

    render() {
        const contentText = this.props.contentText;

        return (
            <footer className="mainFooter">
                <div className="mainFooter__container">
                    <div className="mainFooter__menu">
                        <div className="mainFooter__menu-logo">
                            <Logo className="mainFooter__menu-logo-item" />
                        </div>
                        <div className="mainFooter__menu-item">
                            <Menu
                                colorMenuItems={'#ffffff'}
                                menuItems={contentText.menuItems}
                                isFooter
                            />
                        </div>
                    </div>
                    <div className="mainFooter__extended-menu">
                        <div className="mainFooter__extended-menu-contacts">
                            <Contacts />
                        </div>
                        <div className="mainFooter__extended-menu-menu">
                            <ExtendedMenu
                                menuItems={contentText.menuItemsExtended}
                            />
                        </div>
                    </div>
                    <div className="mainFooter__auxiliary-menu">
                        <div className="mainFooter__auxiliary-menu-copyright">
                            {contentText.copyright}
                        </div>
                        <div className="mainFooter__auxiliary-menu-languages">
                            <div onClick={() => { this.languageChanger('en') }}
                                 className={`mainFooter__auxiliary-menu-languages-item${this.state.currentLanguage === 'en' ? ' active' : ''}`}
                            >
                                <div className="mainFooter__auxiliary-menu-languages-item-value">EN</div>
                            </div>
                            <div onClick={() => { this.languageChanger('de') }}
                                 className={`mainFooter__auxiliary-menu-languages-item${this.state.currentLanguage === 'de' ? ' active' : ''}`}
                            >
                                <div className="mainFooter__auxiliary-menu-languages-item-value">DE</div>
                            </div>
                        </div>
                        <div className="mainFooter__auxiliary-menu-social-networks">
                            <div className="mainFooter__auxiliary-menu-social-networks-item">
                                <Facebook />
                            </div>
                            <div className="mainFooter__auxiliary-menu-social-networks-item">
                                <Linkedin />
                            </div>
                            <div className="mainFooter__auxiliary-menu-social-networks-item">
                                <Instagram />
                            </div>
                            <div className="mainFooter__auxiliary-menu-social-networks-item">
                                <Twitter />
                            </div>
                            <div className="mainFooter__auxiliary-menu-social-networks-item">
                                <Pinterest />
                            </div>
                        </div>
                    </div>
                </div>
            </footer>
        )
    }
}